					</el-button>
					<el-button class="del" v-if="isAuth('yonghu','')" :disabled="dataListSelections.length?false:true" type="danger" @click="deleteHandler()">
						<span class="icon iconfont icon-xihuan" :style='{"margin":"0 2px","fontSize":"14px","color":"#ffffff","fontWeight":"600","height":"40px"}'></span>
						
					</el-button>



					<el-button class="btn18" v-if="isAuth('yonghu','')" type="success" @click="chartDialog1()">
						<span class="icon iconfont icon-xihuan" :style='{"margin":"0 2px","fontSize":"14px","color":"rgb(106, 171, 156)","height":"40px"}'></span>
						
					</el-button>
					<el-button class="btn18" v-if="isAuth('yonghu','')" type="success" @click="chartDialog2()">
						<span class="icon iconfont icon-xihuan" :style='{"margin":"0 2px","fontSize":"14px","color":"rgb(106, 171, 156)","height":"40px"}'></span>
						
					</el-button>
