import Vue from 'vue';
//
import VueRouter from 'vue-router'
Vue.use(VueRouter);
//1.
import Index from '@/views/index'
import Home from '@/views/home'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
    import tushuchanpin from '@/views/modules/tushuchanpin/list'
    import discusstushuchanpin from '@/views/modules/discusstushuchanpin/list'
    import chanpinfenlei from '@/views/modules/chanpinfenlei/list'
    import tushufenlei from '@/views/modules/tushufenlei/list'
    import yonghu from '@/views/modules/yonghu/list'
    import config from '@/views/modules/config/list'
    import discusstushuxinxi from '@/views/modules/discusstushuxinxi/list'
    import tushuxinxi from '@/views/modules/tushuxinxi/list'


//2.   
export const routes = [{
    path: '/',
    name: '',
    component: Index,
    children: [{
      // main
      path: '/',
      name: '',
      component: Home,
      meta: {icon:'', title:'center', affix: true}
    }, {
      path: '/updatePassword',
      name: '',
      component: UpdatePassword,
      meta: {icon:'', title:'updatePassword'}
    }, {
      path: '/pay',
      name: '',
      component: pay,
      meta: {icon:'', title:'pay'}
    }, {
      path: '/center',
      name: '',
      component: center,
      meta: {icon:'', title:'center'}
    }
      ,{
	path: '/tushuchanpin',
        name: '',
        component: tushuchanpin
      }
      ,{
	path: '/discusstushuchanpin',
        name: '',
        component: discusstushuchanpin
      }
      ,{
	path: '/chanpinfenlei',
        name: '',
        component: chanpinfenlei
      }
      ,{
	path: '/tushufenlei',
        name: '',
        component: tushufenlei
      }
      ,{
	path: '/yonghu',
        name: '',
        component: yonghu
      }
      ,{
	path: '/config',
        name: '',
        component: config
      }
      ,{
	path: '/discusstushuxinxi',
        name: '',
        component: discusstushuxinxi
      }
      ,{
	path: '/tushuxinxi',
        name: '',
        component: tushuxinxi
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {icon:'', title:'login'}
  },
  {
    path: '/register',
    name: 'register',
    component: register,
    meta: {icon:'', title:'register'}
  },
  {
    path: '*',
    component: NotFound
  }
]
//3.VueRouter  
const router = new VueRouter({
  mode: 'hash',
  /*hashhistory*/
  routes //  routes: routes
})
const originalPush = VueRouter.prototype.push
//push
VueRouter.prototype.push = function push(location) {
   return originalPush.call(this, location).catch(err => err)
}
export default router;
